								userInfo: resData.data
							});
							this.$eventHub.$emit('doUserInfo', resData.data);
							this.$eventHub.$emit('order', "order");

							uni.reLaunch({
								url: '../index/index?menuFrom=' + this.$data.from
							})


						} else {
							util.errorTips(res.msg);
						}
					}).catch((res) => {
						util.errorTips(res.msg);
					})
				} else {
					api.login({
